.nodeSettingsHeader {
  color: #1d1d1f;
  font-size: 18px;
  font-family: "Open Sans Bold", sans-serif;
  width: 100%;
  padding: 20px 20px 16px;
  line-height: 18px;
  background: #f3f4f8;
  border-radius: 8px 8px 0 0;
  border-bottom: 1px solid #e8ebef;
  box-shadow: 0 5px 15px rgb(232, 235, 239, 0.6);

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nodeSettingsHeader > div {
  display: inline-block;
}
.nodeSettingsHeaderDetails {
  font-size: 12px;
  font-family: "Open Sans Light", sans-serif;
  color: #1d1d1f;
}

.nodeSettingsHeaderDetails:before {
  content: " | ";
  font-size: 14px;
  font-family: "Open Sans Light", sans-serif;
  color: #1d1d1f;
  position: relative;
  bottom: 3px;
}

.nodeSettingsContent {
  height: calc(100% - 55px);
}

.loaderWrapper {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.loading {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 16px;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding-top: 100px;
}

.closeButton {
  cursor: pointer;
}

.nodeSettingsSpinner {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.nodeSettingsSpinner:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #768093;
  border-color: #768093 transparent #768093 transparent;
  animation: nodeSettingsSpinner 1.2s linear infinite;
}

@keyframes nodeSettingsSpinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
