.deviceTypeIcon {
  width: 25px;
  height: 25px;
}

.Section {
  padding: 0 25px;
  height: 50%;
  display: flex;
  align-items: center;
  border-right: 1px solid #e8ebef;
}

.IconSectionBackground {
  background-color: #ecedf4;
  height: 100%;
  z-index: 1;
}

.deviceContentWrapper {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: flex-start;
}

.subTitle {
  font-size: 10px;
  color: #768093;
  font-family: "Open Sans Bold", sans-serif;
}

.text {
  color: #2f3148;
  font-family: "Open Sans Normal", sans-serif;
  display: block;
  font-size: 14px;
}

.deviceType {
  width: 16%;
}

.deviceName {
  width: 28%;
}

.deviceSettingsMenu {
  width: 92px;
  padding: 0 25px;
  align-items: flex-end;
  z-index: 1;
}

.deviceSettingsMenuIcon {
  padding: 2px 5px;
  transition: all 200ms ease-in-out;
  cursor: pointer;
  border: 1px solid transparent;
  vertical-align: middle;
  border-radius: 4px;
}

.deviceSettingsMenuIcon:hover {
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(118, 128, 146, 0.32);
  box-shadow: 0px 3px 16px -3px rgba(118, 128, 146, 0.52);
}

.deviceStatus {
  width: 16%;
}

.cardSummary {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100px;
}

.nodeSettingPageWrapper {
  height: 66.5%;
  display: flex;
}

.placeHolderWidth {
  width: 78px;
}
.nodeSettingPage {
  width: 100%;
  margin: 0 25px;
  border-top: 1px solid #e8ebef;
}

.modalWrapper {
  border: 1px solid rgba(118, 128, 147, 0.7);
  min-width: 98.9%;
  height: 100%;
  border-radius: 8px;
  padding: 0;
  display: flex;
  margin: 0;
  flex-direction: column;
}
