.pageWrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.pageContentWrapper {
  display: flex;
  flex-flow: column;
  padding: 28px 25px;
  width: 100%;
  height: calc(100% - 90px);
  overflow-y: scroll;
  position: relative;
}

.outerCardWrapper {
  width: 100%;
  height: 100px;
  margin-bottom: 15px;
}

.cardWrapper {
  min-height: 100px;
  background: #f9fafb;
  border: 1px solid #e8ebef;
  border-radius: 10px;
  align-items: center;
  position: relative;
  overflow: hidden;
  transition: all 300ms ease-in-out;
  z-index: 0;
}

.cardWrapper:hover,
.cardWrapperSelected {
  box-shadow: 0 5px 30px rgba(118, 128, 146, 0.3);
  border: 1px solid rgba(118, 128, 147, 0.7);
  transition: all 600ms cubic-bezier(0.23, 1, 0.32, 1);
  z-index: 1;
}

.cardWrapperSettingsLoaded {
  height: 77vh;
}

.pageHeaderWrapper {
  display: inline-flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.displayMasterNode {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 14px;
  color: #768093;
}

.displayMasterNode input {
  margin-left: 5px;
}

.nodeSettingsSpinner {
  display: inline-block;
  padding: 28px 35px 20px 21px;
}
.nodeSettingsSpinner:after {
  content: " ";
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #768093;
  border-color: #768093 transparent #768093 transparent;
  animation: nodeSettingsSpinner 1.2s linear infinite;
}

.loading {
  font-family: "Open Sans Bold", sans-serif;
  font-size: 16px;
  display: flex;
  flex-flow: column;
  align-items: center;
  padding-top: 100px;
}

@keyframes nodeSettingsSpinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
